<template>
  <div style="position:relative">
    <div class="container">
      <div class="row">
        <div class="col-6">
          <!-- <img class="gear" :src="'../static/gear.png'" alt=""> -->
          <div class="row corpo" :style="image" ref="capture" oncontextmenu="return false;">
            <div class="col-12 text-center">
              <h3 contenteditable="true" class="titulo" placeholder="Título"></h3>
            </div>

            <div class="col-4 d-flex justify-content-end">
              <div
                class="itens-build bolsa"
                draggable="false"
                @drop="onDrop($event, 'bolsa')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('bolsa')"
                  draggable="false"
                  class="img_inv"
                  :src="bolsa"
                />
              </div>
            </div>
            <div class="col-4 d-flex justify-content-center">
              <div
                class="itens-build elmo"
                @drop="onDrop($event, 'elmo')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('elmo')"
                  draggable="false"
                  class="img_inv"
                  :src="elmo"
                />
              </div>
            </div>
            <div class="col-4">
              <div
                class="itens-build capa"
                @drop="onDrop($event, 'capa')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('capa')"
                  draggable="false"
                  class="img_inv"
                  :src="capa"
                />
              </div>
            </div>
            <div class="col-4 d-flex justify-content-end">
              <div
                class="itens-build arma1"
                @drop="onDrop($event, 'arma1')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('arma1')"
                  draggable="false"
                  class="img_inv"
                  :src="arma1"
                />
              </div>
            </div>
            <div class="col-4 d-flex justify-content-center">
              <div
                class="itens-build armadura"
                @drop="onDrop($event, 'armadura')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('armadura')"
                  draggable="false"
                  class="img_inv"
                  :src="armadura"
                />
              </div>
            </div>
            <div class="col-4">
              <div
                class="itens-build arma2"
                @drop="onDrop($event, 'arma2')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('arma2')"
                  draggable="false"
                  class="img_inv"
                  :src="arma2"
                />
              </div>
            </div>
            <div class="col-4 d-flex justify-content-end">
              <div
                class="itens-build comida"
                @drop="onDrop($event, 'comida')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('comida')"
                  draggable="false"
                  class="img_inv"
                  :src="comida"
                />
              </div>
            </div>
            <div class="col-4 col-4 d-flex justify-content-center">
              <div
                class="itens-build bota"
                @drop="onDrop($event, 'bota')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('bota')"
                  draggable="false"
                  class="img_inv"
                  :src="bota"
                />
              </div>
            </div>
            <div class="col-4">
              <div
                class="itens-build poção"
                @drop="onDrop($event, 'pocao')"
                @dragover.prevent
                @dragenter.prevent
              >
                <img
                  v-on:click.right="LimparImagem('pocao')"
                  draggable="false"
                  class="img_inv"
                  :src="pocao"
                />
              </div>
            </div>
            <br />
            <hr />

            <div class="col-12 resumo">
              <span class="ip" contenteditable="true" placeholder="IP"></span>
              <span>Elmo:</span>
              <span class="resumo_item">{{elmo_desc_ptbr}}</span>
              <br />
              <span>Arma:</span>
              <span class="resumo_item">{{arma1_desc_ptbr}}</span>
              <br />
              <span>Armadura:</span>
              <span class="resumo_item">{{armadura_desc_ptbr}}</span>
              <br />
              <span>Mão Secundária:</span>
              <span class="resumo_item">{{arma2_desc_ptbr}}</span>
              <br />
              <span>Botas:</span>
              <span class="resumo_item">{{bota_desc_ptbr}}</span>
              <br />
              <span>Comida:</span>
              <span class="resumo_item">{{comida_desc_ptbr}}</span>
              <br />
              <span>Poção:</span>
              <span class="resumo_item">{{pocao_desc_ptbr}}</span>
              <br />
            </div>
            <img :src="process.env.ROOT_API +'/static/logo_manifesto_preto.png'" class="logo_img" alt="logo manifesto" />
          </div>
        </div>

        <div class="col-6">
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-default">Buscar</span>
            </div>
            <input
              type="text"
              v-model="searchQuery"
              class="form-control"
              aria-label="Exemplo do tamanho do input"
              aria-describedby="inputGroup-sizing-default"
            />
          </div>

          <!-- tabela -->
          <div style="max-height: 630px; overflow:auto">
            <table class="table table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Imagem</th>
                  <th scope="col">Nome do Item</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in resultQuery"
                  :key="index"
                  draggable
                  @dragstart="startDrag($event, item)"
                >
                  <th scope="row">1</th>
                  <td>
                    <img :src="item.url_img" class="img-lista" alt />
                  </td>
                  <td>{{item.desc_ptbr}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- tabela -->
      </div>
      <div class="row" style="margin-top:5px">
        <div class="col-6 d-flex align-items-center">
          <div class="btn btn-dark" style="width:100%" @click="DownloadImagem">{{txbtnprint}}</div>
        </div>
        <div class="col-6">
          <p class="instrucoes">- Arraste a imagem para as posições no inventário</p>
          <p class="instrucoes">- Botão direito do mouse para retirar um item do inventário</p>
          <p
            class="instrucoes"
          >- Pode escrever um título no campo superior (visível apenas ao passar o mouse)</p>
          <p
            class="instrucoes"
          >- Pode escrever o ip no campo abaixo da poção (visível apenas ao passar o mouse)</p>
          <p
            class="instrucoes"
          >- Clique em gerar imagem, depois clique com o direito para salvar ou copiar imagem</p>
        </div>
      </div>
    </div>
    <div id="imagem-print" v-show="verimagemprint" class>
      <div class="fechar" @click="FecharImagem">
        <img :src="'/static/fechar.png'" />
      </div>
    </div>
  </div>
</template>

<script>
import ListaItens from "../js/items.js";
import html2canvas from "html2canvas";
// import ListaItens from "../js/items_old.js";
// import DBItens from "../js/dbitens.js";

export default {
  name: "Inicio",
  data() {
    return {
      searchQuery: "",
      verimagemprint: false,
      placeholder_titulo: "",
      placeholder_ip: "",
      txbtnprint: "Gerar Imagem",
      image: {
        backgroundImage: "url(" + process.env.ROOT_API + "/static/bg.png)",
        "background-position": "center",
      },
      itens: ListaItens.Items,
      // dbitens: DBItens.Items,
      arma1: process.env.ROOT_API + "/static/BRANCO.png",
      arma1_desc_ptbr: "-//-",
      bolsa: process.env.ROOT_API + "/static/ZERO.png",
      bolsa_desc_ptbr: "-//-",
      elmo: process.env.ROOT_API + "/static/BRANCO.png",
      elmo_desc_ptbr: "-//-",
      capa: process.env.ROOT_API + "/static/CAPA.png",
      capa_desc_ptbr: "-//-",
      armadura: process.env.ROOT_API + "/static/BRANCO.png",
      armadura_desc_ptbr: "-//-",
      arma2: process.env.ROOT_API + "/static/ZERO.png",
      arma2_desc_ptbr: "-//-",
      comida: process.env.ROOT_API + "/static/FOOD.png",
      comida_desc_ptbr: "-//-",
      bota: process.env.ROOT_API +"/static/BRANCO.png",
      bota_desc_ptbr: "-//-",
      pocao: process.env.ROOT_API + "/static/POCAO.png",
      pocao_desc_ptbr: "-//-",
      lista: [],
      // msg: 'Welcome to Your Vue.js App'
    };
  },
  created() {
    // var lista = []
    // itens = this.itens.Items
    // for(let i = 0; i <= itens.lenght; i+=1){
    //   lista[i].ref = itens[i].LocalizationNameVariable
    //   lista[i].desc = itens[i].LocalizedNames
    // }
    // this.lista = lista
  },
  computed: {
    resultQuery() {
      if (this.searchQuery) {
        return this.itens.filter((item) => {
          return this.searchQuery
            .toLowerCase()
            .split(" ")
            .every((v) => item.desc_ptbr.toLowerCase().includes(v));
        });
        console.log("passou");
      } else {
        return this.itens;
      }
    },
  },
  methods: {
    Hover(evt) {
      evt.preventDefault();
      onsole.log("hover");
    },

    startDrag(evt, item) {
      console.log(evt);
      evt.dataTransfer.dropEffect = "move";
      evt.dataTransfer.effectAllowed = "move";
      evt.dataTransfer.setData("url_img", item.url_img);
      evt.dataTransfer.setData("ref", item.ref);
      evt.dataTransfer.setData("desc_ptbr", item.desc_ptbr);
      evt.stopPropagation();
    },

    LimparImagem(ref) {
      if (ref == "arma1") {
        this.arma1 = process.env.ROOT_API + "/static/BRANCO.png";
        this.arma1_desc_ptbr = "";
      }
      if (ref == "bolsa") {
        this.bolsa = process.env.ROOT_API + "/static/ZERO.png";
        this.bolsa_desc_ptbr = "";
      }
      if (ref == "elmo") {
        this.elmo = process.env.ROOT_API + "/static/BRANCO.png";
        this.elmo_desc_ptbr = "";
      }
      if (ref == "capa") {
        this.capa = process.env.ROOT_API + "/static/CAPA.png";
        this.capa_desc_ptbr = "";
      }
      if (ref == "armadura") {
        this.armadura = process.env.ROOT_API + "/static/BRANCO.png";
        this.armadura_desc_ptbr = "";
      }
      if (ref == "arma2") {
        this.arma2 = process.env.ROOT_API + "/static/ZERO.png";
        this.arma2_desc_ptbr = "";
      }
      if (ref == "comida") {
        this.comida = process.env.ROOT_API + "/static/FOOD.png";
        this.comida_desc_ptbr = "";
      }
      if (ref == "bota") {
        this.bota = process.env.ROOT_API + "/static/BRANCO.png";
        this.bota_desc_ptbr = "";
      }
      if (ref == "pocao") {
        this.pocao = process.env.ROOT_API + "/static/POCAO.png";
        this.pocao_desc_ptbr = "";
      }
    },

    onDrop(evt, list) {
      const itemID = evt.dataTransfer.getData("url_img");
      const ref = evt.dataTransfer.getData("ref");
      const desc_ptbr = evt.dataTransfer.getData("desc_ptbr");
      evt.stopPropagation();
      evt.preventDefault();

      if (list == "arma1") {
        this.arma1 = process.env.ROOT_API + itemID;
        this.arma1_desc_ptbr = desc_ptbr;
      }
      if (list == "bolsa") {
        this.bolsa = process.env.ROOT_API + itemID;
        this.bolsa_desc_ptbr = desc_ptbr;
      }
      if (list == "elmo") {
        this.elmo = process.env.ROOT_API + itemID;
        this.elmo_desc_ptbr = desc_ptbr;
      }
      if (list == "capa") {
        this.capa = process.env.ROOT_API + itemID;
        this.capa_desc_ptbr = desc_ptbr;
      }
      if (list == "armadura") {
        this.armadura = process.env.ROOT_API + itemID;
        this.armadura_desc_ptbr = desc_ptbr;
      }
      if (list == "arma2") {
        this.arma2 = process.env.ROOT_API + itemID;
        this.arma2_desc_ptbr = desc_ptbr;
      }
      if (list == "comida") {
        this.comida = process.env.ROOT_API + itemID;
        this.comida_desc_ptbr = desc_ptbr;
      }
      if (list == "bota") {
        this.bota = process.env.ROOT_API + itemID;
        this.bota_desc_ptbr = desc_ptbr;
      }
      if (list == "pocao") {
        this.pocao = process.env.ROOT_API + itemID;
        this.pocao_desc_ptbr = desc_ptbr;
      }
      // console.log(itemID);

      // const item = this.lista.find(item => item.ref == itemID)
      // item.list = list
    },
    showCaptureRef() {
      console.log("this.$refs.capture: " + this.$refs.capture);
      let vc = this;
      return vc.$refs.capture;
    },
    FecharImagem() {
      this.verimagemprint = false;
      document.getElementById("imgcanvas").remove();
    },
    DownloadImagem() {
      let vc = this;
      var self = this;
      var ele = document.getElementById("imagem-print");
      // ele.innerHTML = '';
      self.txbtnprint = "Gerando Imagem...";
      // ele.removeChild(document.getElementById('imgcanvas')) ;

      html2canvas(vc.showCaptureRef())
        .then((canvas) => {
          canvas.id = "imgcanvas";
          ele.appendChild(canvas);
          // ele.className = "imagem-print-canvas";
          // canvas is the final rendered <canvas> element

          self.txbtnprint = "Gerar Imagem";
          self.verimagemprint = true;
        })
        .catch((error) => {
          console.log("erro ao criar imagem", error);
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.itens-build {
  width: 110px;
  height: 110px;
  margin: 0px;
  /* background-color: cadetblue; */
  /* border: solid 1px black */
}
.itens-build:hover {
  background-color: rgba(0, 0, 0, 0.08);
}
.corpo {
  /* border: solid 2px #d6d6d6; */
  padding: 30px;
  position: relative;
  margin: 5px;
}
#imagem-print {
  -webkit-box-shadow: 0px 0px 0px 7000px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 0px 0px 0px 7000px rgba(0, 0, 0, 0.7);
  box-shadow: 0px 0px 0px 7000px rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0px;
  left: 12%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ip {
  position: absolute;
  right: 10%;
  min-width: 90px;
  font-weight: bold;
}
.img-lista {
  cursor: move;
  width: 80px;
}
.img_inv {
  width: 100%;
}
.gear {
  position: absolute;
  top: -28px;
  left: 10px;
}
.logo_img {
  height: 85px;
  margin-top: 5px;
}
.resumo_item {
  font-weight: bold;
}
.fechar {
  position: absolute;
  top: -40px;
  right: -40px;
  width: 30px;
  height: 30px;
  text-align: center;
  color: white;
  font-weight: bold;
  padding: 0px;
  margin: 0px;
}
.fechar img {
  width: 50px;
}
.titulo:hover {
  background-color: rgba(0, 0, 0, 0.08);
}

.titulo:hover[placeholder]:empty:before {
  content: attr(placeholder);
  color: #888;
}
.ip:hover {
  background-color: rgba(0, 0, 0, 0.08);
}
.ip:hover[placeholder]:empty:before {
  content: attr(placeholder);
  color: #888;
}
.instrucoes {
  font-size: 0.8rem;
  margin: 0.1rem;
  font-style: italic;
}
</style>



